import React, { useRef, useState, useEffect } from 'react';
import styled from 'styled-components';

const ContainerWrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 20px;
  height: 932px;
  justify-content: center;
`;

const Container = styled.div`
  font-size: 20px;
  font-weight: 900;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 120px;
`;

const Box = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 50px;
  position: relative;
`;

const Title = styled.div`
  font-size: 24px;
  font-weight: 600;
  color: #2b1b1f;
`;
const Back = styled.div`
  width: 600px;
  height: 168px;
  background-color:#ffffff;
   display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  border-radius: 14px;
    box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.15); /*    */
   border: 3px solid #ffffff;

    &:hover{
    border: 3px solid #AAA4A5;
     & >.Circle{
     background-color:#332428;
     display:block;
     }
  }
`;
const SliderWrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 70px;
  position: relative;
  width: 490px;
  height: 20px;
  justify-content: center;
  border-radius: 50px;
`;
const SliderInput = styled.input`
  width: 100%;
  height: 100%;
  background: transparent; /*    */
  outline: none;
  -webkit-appearance: none;
  appearance: none;
  padding: 0;
  margin: 0;
  border-radius: 50px;
  position: relative;

  /*   (, ) */
  &::-webkit-slider-runnable-track {
    background: ${(props) => {
      const percentage = ((props.value - props.min) / (props.max - props.min)) * 100;
      return `linear-gradient(to right, ${props.startColor} 0%, ${props.endColor} ${percentage}%, #ddd ${percentage}%)`;
    }};
    width: 100%;
    height: 18px;
    border-radius: 50px;
    border: none;
  }

  /*   () */
  &::-moz-range-track {
    background: ${(props) => {
      const percentage = ((props.value - props.min) / (props.max - props.min)) * 100;
      return `linear-gradient(to right, ${props.startColor} 0%, ${props.endColor} ${percentage}%, #ddd ${percentage}%)`;
    }};
    width: 100%;
    height: 20px;
    border-radius: 50px;
    border: none;
  }

  /*   */
  &::-webkit-slider-thumb {
    -webkit-appearance: none;
        box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.2); /*    */
    appearance: none;
    width: 42px;
    height: 42px;
    background-color: ${props => props.thumbColor}; /*   */
    border-radius: 50px;
    border: 8px solid #ffffff;
    cursor: pointer;
    transition: background-color 0.3s ease;
    position: relative;
    transform: translate(2px,-9px); /*     */

    &:hover{
       box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.5);
    }

  }

  &::-moz-range-thumb {
    width: 40px;
    height: 40px;
    background-color: ${props => props.thumbColor}; /*   */
    border-radius: 50px;
    border: 8px solid #ffffff;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
`;

const SliderValue = styled.span`
  font-size: 22px;
  font-weight: bold;
  color: #332428;
  width:auto;
  white-space: nowrap;
  padding:4px 10px;
  border-radius:14px;
  position: absolute;
  text-align: center;
  top: -54px;
  left: ${(props) => `${props.thumbPosition-10}px`}; /*   20px  */
  transform: translateX(-50%);
  ackground-color:#ffffff;
  box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.2);
`;
const Circle = styled.div`
    width: 24px;
    height: 24px;
    background-color: #ffffff;
    border-radius: 50px;
    position: absolute;
    top: 70px;
    right:-46px;
`

const calculatePositionInPx = (value, min, max, sliderWidth, thumbWidth) => {
  const percentage = (value - min) / (max - min);  //    
  const thumbPosition = percentage * (sliderWidth - thumbWidth);  //   
  return thumbPosition + thumbWidth / 2;  //     
};

const BlobUserCustomWaterEx = ({ ET, setET, W, setWT }) => {
  const sliderRef1 = useRef(null); //    ref
  const sliderRef2 = useRef(null); //    ref
  const [thumbPosition1, setThumbPosition1] = useState(0);
  const [thumbPosition2, setThumbPosition2] = useState(0);

  const handleETSliderChange = (e) => {
    setET(parseInt(e.target.value));
    const sliderWidth = sliderRef1.current.offsetWidth; //    
    const thumbWidth = 34; //   (36px )
    const thumbPos = calculatePositionInPx(e.target.value, e.target.min, e.target.max, sliderWidth, thumbWidth);
    setThumbPosition1(thumbPos); //      
  };

  const handleWTSliderChange = (e) => {
    setWT(parseInt(e.target.value));
    const sliderWidth = sliderRef2.current.offsetWidth; //      
    const thumbWidth = 34; //   (36px )
    const thumbPos = calculatePositionInPx(e.target.value, e.target.min, e.target.max, sliderWidth, thumbWidth);
    setThumbPosition2(thumbPos); //      
  };

  useEffect(() => {
    //    
    if (sliderRef1.current && sliderRef2.current) {
      handleETSliderChange({ target: sliderRef1.current });
      handleWTSliderChange({ target: sliderRef2.current });
    }
  }, []);

  return (
    <ContainerWrapper>
      <Container>
        <Box>
          <Title>Exercise</Title>
          <Back>
          <SliderWrapper>
            <SliderInput
              ref={sliderRef1}
              type="range"
              min="10"
              max="60"
              step="1"
              value={ET}
              onChange={handleETSliderChange}
              thumbPosition={thumbPosition1}
              thumbColor="#ffffff"
              startColor="#FFD1D1"  /*      () */
              endColor="#FF6464"  /*      () */
            />
            <SliderValue thumbPosition={thumbPosition1}>
              {ET} 
            </SliderValue>
            
          </SliderWrapper>
          <Circle className='Circle'></Circle>
          </Back>
        </Box>

        <Box>
          <Title>Water Intake</Title>
          <Back>
          <SliderWrapper>
            <SliderInput
              ref={sliderRef2}
              type="range"
              min="100"
              max="1200"
              step="1"
              value={W}
              onChange={handleWTSliderChange}
              thumbPosition={thumbPosition2}
              thumbColor="#ffffff"
              startColor="#E0F1FF"  /*      () */
              endColor="#64B9FF"  /*      () */
            />
            <SliderValue thumbPosition={thumbPosition2}>
              {W} ml
            </SliderValue>
          </SliderWrapper>
          <Circle className='Circle'></Circle>
          </Back>
        </Box>
      </Container>
    </ContainerWrapper>
  );
};

export default BlobUserCustomWaterEx;